import React from "react";
import "./Production.css";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";

function Production() {
  const [selectedFile, setSelectedFile] = useState({
    file1: "",
    file2: "",
    file3: "",
    file4: "",
  });

  const [showNewOrder, setshowNewOrder] = useState(false);
  const navigate = useNavigate();

  const [formValues, setFormValues] = useState({
    nameOrder: "",
    category: " ",
    descOrder: "",
    quantity: "",
    materialOrder: " ",
    sample: " ",
    delivery: "",
    country: "",
    deliveryType: "-",
    orderDeadline: "",
    payment: "",
    moreServices: "",
    comments: "",
    users_permissions_users: "",
  });

  const onChangeSelected = (e) => {
    setFormValues((formValues) => {
      return {
        ...formValues,

        [e.target.name]: e.target.value,
        users_permissions_users: localStorage.getItem("id"),
      };
    });
  };

  const onClickBackNewOrder = () => {
    setshowNewOrder(true);
    navigate("/new-order");
  };

  const onFinish = async (e) => {
    e.preventDefault();

    if (
      !formValues.nameOrder ||
      !formValues.descOrder ||
      !formValues.quantity ||
      !formValues.orderDeadline ||
      !formValues.payment ||
      !formValues.moreServices ||
      !formValues.country ||
      !formValues.comments ||
      !selectedFile
    ) {
      alert("  ");
    } else {
      const formData = new FormData();
      formData.append("files", selectedFile.file1);
      formData.append("files", selectedFile.file2);
      formData.append("files", selectedFile.file3);
      formData.append("files", selectedFile.file4);

      const uploadResponse = await axios.post(
        "https://strapi.belgi.kg/api/upload",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
          params: {
            ref: "production",
            refId: localStorage.getItem("id"),
            field: "mediaProduct",
          },
        }
      );

      const fileId = uploadResponse.data[0].id;
      const fileId2 = uploadResponse.data[1].id;
      const fileId3 = uploadResponse.data[2].id;
      const fileId4 = uploadResponse.data[3].id;

      setFormValues((formValues) => {
        return {
          ...formValues,
          mediaProduction: fileId,
        };
      });

      await axios
        .post("https://strapi.belgi.kg/api/productions", {
          data: {
            ...formValues,
            mediaProduct: fileId,
            mediaProduct2: fileId2,
            mediaProduct3: fileId3,
            mediaProduct4: fileId4,
          },
        })
        .then((res) => localStorage.setItem("idProduct", res.data.data.id));

      const token = "6059462033:AAHMTNU6CakxUuMjoaiayqgkAN1R-cyxQ-A";
      const chat_id = "-1001950653999";
      const button = {
        text: "",
        url: `https://strapi.belgi.kg/allProductList/${
          localStorage.getItem("idProduct") && localStorage.getItem("idProduct")
        }`,
      };

      const inlineKeyboard = {
        inline_keyboard: [[button]],
      };

      const keyboardJSON = JSON.stringify(inlineKeyboard);

      const url =
        "https://api.telegram.org/bot" +
        token +
        "/sendMessage?chat_id=" +
        chat_id +
        "&parse_mode=html&text=" +
        encodeURIComponent(
          "<b><i> :</i></b>  " +
            formValues.nameOrder +
            "\n <b><i>:</i></b>  " +
            formValues.category +
            "\n<b><i> :</i></b>  " +
            formValues.descOrder +
            "\n<b><i>:</i></b>  " +
            formValues.quantity +
            "\n<b><i>  :</i></b>  " +
            formValues.materialOrder +
            "\n<b><i>:</i></b>  " +
            formValues.sample +
            "\n<b><i>:</i></b>  " +
            formValues.delivery +
            "\n<b><i>:</i></b>  " +
            formValues.country +
            "\n<b><i> :</i></b>  " +
            formValues.deliveryType +
            "\n<b><i>  :</i></b>  " +
            formValues.orderDeadline +
            "\n<b><i> :</i></b>  " +
            formValues.payment +
            "\n<b><i> :</i></b>  " +
            formValues.moreServices +
            "\n<b><i>  :</i></b>  " +
            formValues.comments +
            // "\n<b><i> :</i></b>  " +
            // formValues.mediaProduct +
            "\n"
        ) +
        "&reply_markup=" +
        encodeURIComponent(keyboardJSON);
      await fetch(url);

      navigate("/my-trades");
    }
  };

  return (
    <>
      <div className="production-container">
        <div className="production-content">
          <h3>:</h3>
          <form className="form-production">
            <label>
               :
              <input
                id="production-inputs"
                className="production-nameOrder"
                placeholder=":  "
                name="nameOrder"
                onChange={onChangeSelected}
                required
                maxLength="100"

              />
            </label>
            <label className="production-lable-block">
               :
              <select
                id="production-inputs"
                className="production-category"
                name="category"
                onChange={onChangeSelected}
              >
                <option value=" "> </option>
                <option value=" "> </option>
                <option value=" "> </option>
                <option value=""></option>
              </select>
            </label>
            <label className="production-lable-block">
               :
              <textarea
                style={{ width: "100%", height: "150px" }}
                id="production-inputs"
                className="production-descripton"
                placeholder=":  /"
                name="descOrder"
                onChange={onChangeSelected}
                maxLength="500"

              />
            </label>
            <label className="production-lable-block">
              :
              <input
                type="number"
                id="production-inputs"
                className="production-descripton"
                name="quantity"
                onChange={onChangeSelected}
                maxLength="50"

              />
            </label>
            <label className="production-lable-block">
                :
              <select
                id="production-inputs"
                className="production-material"
                name="materialOrder"
                onChange={onChangeSelected}
              >
                <option value=" ">
                   
                </option>
                <option value=" ">
                   
                </option>
                <option value=" "> </option>
              </select>
            </label>
            <label className="production-lable-block">
              :
              <select
                id="production-inputs"
                className="production-sample"
                name="sample"
                onChange={onChangeSelected}
              >
                <option value=" ">
                   
                </option>
                <option value=" "> </option>
              </select>
            </label>

            <label className="production-lable-block">
              :
              <select
                id="production-inputs"
                className="production-delivery"
                name="delivery"
                onChange={onChangeSelected}
              >
                <option value=""></option>
                <option value=""></option>
                <option value=" "> </option>
              </select>
            </label>

            <label className="production-lable-block">
              :
              <input
                type="text"
                id="production-inputs"
                className="production-payment"
                name="country"
                onChange={onChangeSelected}
                placeholder=""
                maxLength="100"

              />
            </label>

            <label className="production-lable-block">
               :
              <select
                id="production-inputs"
                className="production-deliveryType"
                name="deliveryType"
                onChange={onChangeSelected}
              >
                <option value="-">-</option>
                <option value=" "> </option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </label>

            <label className="production-lable-block">
                :
              <input
                type="date"
                id="production-inputs"
                className="production-orderDeadline"
                name="orderDeadline"
                onChange={onChangeSelected}
              />
            </label>
            <label className="production-lable-block">
               :
              <input
                type="text"
                id="production-inputs"
                className="production-payment"
                name="payment"
                onChange={onChangeSelected}
                placeholder=": ,   .."
                maxLength="100"
              />
            </label>

            <label className="production-lable-block">
               :
              <input
                type="text"
                id="production-inputs"
                className="production-moreServices"
                name="moreServices"
                onChange={onChangeSelected}
                placeholder=": ,   .."
                maxLength="100"

              />
            </label>

            <label className="production-lable-block">
                :
              <textarea
                style={{ width: "100%", height: "100px", resize: "none" }}
                id="production-inputs"
                className="production-comments"
                name="comments"
                onChange={onChangeSelected}
                maxLength="500"
              />
            </label>

            <label className="production-lable-block">
               :
              <input
                type="file"
                id="production-inputs"
                name="mediaProduct"
                onChange={(e) => {
                  setSelectedFile((selectedFile) => {
                    return {
                      ...selectedFile,
                      file1: e.target.files[0],
                    };
                  });
                }}
                multiple
              />
              <input
                type="file"
                id="production-inputs"
                name="mediaProduct"
                onChange={(e) => {
                  setSelectedFile((selectedFile) => {
                    return {
                      ...selectedFile,
                      file2: e.target.files[0],
                    };
                  });
                }}
                multiple
              />
              <input
                type="file"
                id="production-inputs"
                name="mediaProduct"
                onChange={(e) => {
                  setSelectedFile((selectedFile) => {
                    return {
                      ...selectedFile,
                      file3: e.target.files[0],
                    };
                  });
                }}
                multiple
              />
              <label htmlFor="production-inputs">
                 : (  PDF)
              </label>
              <input
                type="file"
                id="production-inputs"
                name="mediaProduct"
                onChange={(e) => {
                  setSelectedFile((selectedFile) => {
                    return {
                      ...selectedFile,
                      file4: e.target.files[0],
                    };
                  });
                }}
                multiple
              />
            </label>

            <button
              type="submit"
              className="btn-production-send-form"
              style={{ marginBottom: "10px" }}
              onClick={onFinish}
            >
                
            </button>
          </form>
          <button
            type="primary"
            className="btn-production-back"
            onClick={onClickBackNewOrder}
          >
            
          </button>
          {showNewOrder && navigate("/new-order")}
        </div>
      </div>
    </>
  );
}

export default Production;
